import React, { ReactElement, useMemo } from "react";
import { IWindow } from "../../../../interface";
import * as C from "./styles";
import Finder from "../../../../apps/finder";

const DefaultComp = ({ icon, name }: IWindow) => (
  <div
    style={{
      background: "#fff",
      height: "100%",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    }}
  >
    <img
      className="componentImage"
      src={`/assets/images/icons/appicons/${(icon || name).toLowerCase()}.png`}
      alt="icon"
    />
  </div>
);

const compMap: Record<string, (props: IWindow) => ReactElement> = {
  Finder: Finder,
};

export const WindowComponent = ({ name, icon, getDimensions }: IWindow) => {
  const Comp = useMemo(() => compMap?.[name || ""] || DefaultComp, [name]);
  return (
    <C.Container>
      <Comp name={name} icon={icon} getDimensions={getDimensions} />
    </C.Container>
  );
};
