import { motion } from "framer-motion";
import { CenteredPage } from "./styles";

const Contact = () => {
  return (
    <CenteredPage>
      <motion.div className="title contact">
        Connect with the Code Conjurer
        <br />
        <small>Let's Craft Digital Dreams Together!</small>
      </motion.div>
      <motion.div className="about" style={{ cursor: "default" }}>
        <a href="https://linkedin.com/in/niteshdangi" target="_blank">
          <motion.img
            src="/assets/images/portfolio/linkedin.png"
            className="contact-way"
          />
        </a>
        <a target="_blank" href="https://instagram.com/nitesh_dangi_">
          <motion.img
            src="/assets/images/portfolio/instagram.webp"
            className="contact-way"
          />
        </a>
        <a target="_blank" href="mailto:niteshdangi528@gmail.com">
          <motion.img
            src="/assets/images/portfolio/gmail.png"
            className="contact-way"
          />
        </a>
        <a target="_blank" href="https://github.com/niteshdangi">
          <motion.img
            src="/assets/images/icons/appicons/github_profile.png"
            className="contact-way"
          />
        </a>
      </motion.div>
    </CenteredPage>
  );
};

export default Contact;
