import React, { useMemo, useRef } from "react";
import * as C from "./styles";
import * as uuid from "uuid";
import { WindowComponent } from "./components/WindowComponent";

import Draggable from "react-draggable";
import {
  focusWindow,
  removeWindow,
  selectCurrentWindow,
} from "../../global-state";
import { useDispatch, useSelector } from "react-redux";
import { IWindow } from "../../interface";

const Window = (windowObj: IWindow) => {
  const { name, id, getDimensions, getPosition } = windowObj;
  const currentWindow = useSelector(selectCurrentWindow);
  const dispatch = useDispatch();
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const closeWindow = (e: any) => {
    e?.stopPropagation?.();
    if (id === currentWindow?.id) {
      dispatch(focusWindow({ name: null, id: null }));
    }
    dispatch(removeWindow(id));
  };
  const handleFocus = () => {
    dispatch(focusWindow({ id, name }));
  };
  const classId = useRef("drag" + uuid.v4());

  const [dimensions, position] = useMemo(
    () => [getDimensions?.(), getPosition?.()],
    [id]
  );

  const isStandaloneWindow = position?.[0] === -1 && position?.[1] === -1;

  return (
    <C.WindowContainer id={"window-" + id}>
      <Draggable
        bounds={{ top: 0 }}
        defaultPosition={{
          x: position?.[0] || -window.innerWidth / 4,
          y: position?.[1] || 20,
        }}
        handle={"." + classId.current}
        disabled={isStandaloneWindow}
      >
        <C.Container
          className={
            (currentWindow?.id === id ? "focused" : "") +
            (isStandaloneWindow ? " standalone" : "")
          }
          id={"window-container-" + id}
          onClick={handleFocus}
          style={
            dimensions?.length === 2
              ? {
                  width: dimensions[0],
                  height: dimensions[1],
                }
              : {}
          }
        >
          {!isStandaloneWindow && (
            <C.Top className={classId.current}>
              <div className="window-btn close" onClick={closeWindow}>
                <img src={`/assets/images/icons/window/close.png`} alt="" />
              </div>
              <div className="window-btn minimize" onClick={closeWindow}>
                <img src={`/assets/images/icons/window/minimize.png`} alt="" />
              </div>
            </C.Top>
          )}

          <C.Body>
            {name !== "none" && <WindowComponent {...windowObj} />}
          </C.Body>
        </C.Container>
      </Draggable>
    </C.WindowContainer>
  );
};
export default Window;
