import { motion, useScroll, useTransform } from "framer-motion";
import { Page } from "./styles";
import { ReactComponent as ComputerGraphic } from "./assets/computer_graphics.svg";
import { RefObject, useEffect, useState } from "react";
import { animate, delay, initial } from "./utils";

const Landing = ({
  containerRef,
}: {
  containerRef: RefObject<HTMLDivElement>;
}) => {
  const [ref, setRef] = useState<RefObject<HTMLDivElement>>();
  const { scrollYProgress } = useScroll({
    container: ref,
  });
  const top1 = useTransform(scrollYProgress, [0, 0.15], [0, -300]);
  const top2 = useTransform(scrollYProgress, [0, 0.2], [0, -300]);
  const top3 = useTransform(scrollYProgress, [0, 0.1, 0.25], [0, 100, -300]);
  const [isAboutToScroll, setIsAboutToScroll] = useState(false);

  useEffect(() => {
    setRef(containerRef);
  }, []);

  const onAboutMouseEnter = () => {
    setIsAboutToScroll(true);
  };
  const onAboutMouseLeave = () => {
    setIsAboutToScroll(false);
  };

  return (
    <Page>
      <motion.div
        initial={{ opacity: 0, scale: 0.1 }}
        animate={{ opacity: 1, scale: 1 }}
        className="gradient_bottom_left"
      />
      <motion.div
        initial={{ opacity: 0, scale: 0.1 }}
        animate={{ opacity: 1, scale: 1 }}
        className="gradient_top_center"
      />
      <motion.div className="bg_dots">
        {Array.from({ length: 20 }, (_, inx) => (
          <div key={inx}>
            {Array.from({ length: 30 }, (__, ind) => (
              <div key={ind}>
                <div />
              </div>
            ))}
          </div>
        ))}
      </motion.div>
      <div>
        <motion.div style={{ y: top1 }}>
          <motion.div
            animate={{ y: isAboutToScroll ? -100 : 0 }}
            transition={delay(0)}
          >
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(0)}
              className="tag"
            >
              {"<head>"}
            </motion.div>
          </motion.div>
          <motion.div
            animate={{ y: isAboutToScroll ? -90 : 0 }}
            transition={delay(0)}
          >
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(0.1)}
              className="hi"
            >
              {"<Hi, I'm />"}
            </motion.div>
          </motion.div>
        </motion.div>
        <motion.div style={{ y: top2 }}>
          <motion.div
            animate={{ y: isAboutToScroll ? -80 : 0 }}
            transition={delay(0)}
          >
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(0.2)}
              className="name"
            >
              {"NITESH KUMAR".split("").map((char) => (
                <span key={char}>{char}</span>
              ))}
            </motion.div>
          </motion.div>
          <motion.div
            animate={{ y: isAboutToScroll ? -70 : 0 }}
            transition={delay(0)}
          >
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(0.3)}
              className="tag"
            >
              {"</head>"}
            </motion.div>
          </motion.div>
          <motion.div
            animate={{ y: isAboutToScroll ? -60 : 0 }}
            transition={delay(0)}
          >
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(0.7)}
              className="tag"
            >
              {"<body>"}
            </motion.div>
          </motion.div>
          <motion.div
            animate={{ y: isAboutToScroll ? -50 : 0 }}
            transition={delay(0)}
          >
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(0.8)}
              className="intro"
            >
              {`Embarking on a digital odyssey, I am a Frontend Maestro and Backend
        Alchemist rolled into one, sculpting visually captivating realms and
        engineering robust backbones. With a flair for frontend finesse and
        a dash of mobile magic, I dance between pixels and protocols,
        orchestrating symphonies that redefine the art of user-centric
        technology.`
                .split(" ")
                .map((char, inx) => (
                  <span key={char + inx}> {char}</span>
                ))}
            </motion.div>
          </motion.div>

          <motion.div animate={{ y: 0 }} transition={delay(0)}>
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(0.9)}
              className="about-button"
              onMouseEnter={onAboutMouseEnter}
              onMouseLeave={onAboutMouseLeave}
              onClick={() =>
                containerRef.current?.scrollTo({
                  top: window.innerHeight,
                  behavior: "smooth",
                })
              }
            >
              <span>{"<scroll> "}</span>About Me{" "}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 16 16"
                width="16"
                height="16"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              >
                <path d="M9 16l6-6-6-6" />
              </svg>{" "}
              <span>{" </scroll>"}</span>
            </motion.div>
            <motion.div
              initial={initial}
              animate={animate}
              transition={delay(1)}
              className="tag"
            >
              {"</body>"}
            </motion.div>
          </motion.div>
        </motion.div>
      </div>
      <motion.div style={{ y: top3 }}>
        <motion.div
          animate={{ y: isAboutToScroll ? -50 : 0 }}
          transition={delay(0)}
        >
          <motion.div
            initial={initial}
            animate={animate}
            transition={delay(0.4)}
          >
            <ComputerGraphic className="computer_graphic" />
          </motion.div>
        </motion.div>
      </motion.div>
    </Page>
  );
};

export default Landing;
