import { motion } from "framer-motion";
import styled, { keyframes } from "styled-components";

export const Container = styled.div`
  background: #000000;
  backdrop-filter: blur(40px);
  width: 100%;
  height: 100%;
  padding: 40px;
  color: #fff;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
  position: relative;
  scroll-behavior: smooth;

  @media (max-width: 1080px) {
    margin-left: -40px;
  }
`;
const breatheAnimation = keyframes`
 0% { transform: translatey(0) }
 50% { transform: translatey(-300px) }
 100% { transform: translatey(0) }
`;
const breatheAnimationSmall = keyframes`
 0% { transform: translatey(0) }
 50% { transform: translatey(-20px) }
 100% { transform: translatey(0) }
`;
export const Page = styled(motion.div)`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  scroll-snap-align: center;
  padding-left: 50px;
  position: relative;

  @media (max-width: 1080px) {
    flex-direction: column-reverse;
  }
  .tag {
    font-size: 12px;
    color: gray;
    margin-left: -20px;
    margin-bottom: 20px;
    margin-top: 20px;
  }

  .intro {
    color: beige;
    cursor: none;
    & > span {
      transition: all 0.9s ease-out;
    }
    & > span:hover {
      transition: all 0s ease-in;
      color: fuchsia;
    }
  }

  .hi {
    color: aquamarine;
    font-size: 1.5rem;
  }
  .name {
    font-size: 4rem;
    font-weight: 600;
    text-shadow: 1px black;
    margin-top: 10px;
    margin-bottom: 10px;

    & > span:hover {
      transition: all 0s ease-in;
      color: aqua;
    }
  }

  .computer_graphic {
    width: 50vw;
    max-height: 70vh;

    .code_graph path {
      transition: all 0.3s;
      animation-name: ${breatheAnimation};
      animation-duration: 2s;
      animation-iteration-count: infinite;
      animation-timing-function: ease-in-out;
    }
    .code_graph:hover path {
      fill: blue !important;
    }
    #path430:hover {
      fill: yellow !important;
    }
  }

  .gradient_bottom_left {
    background: fuchsia;
    position: absolute;
    bottom: 100px;
    left: 100px;
    width: 100px;
    height: 100px;
    filter: blur(100px);
    z-index: -1;
  }
  .gradient_top_center {
    background: fuchsia;
    position: absolute;
    top: 100px;
    left: 40%;
    width: 100px;
    height: 100px;
    filter: blur(100px);
    z-index: -1;
  }
  .bg_dots {
    position: absolute;
    bottom: -200px;
    right: 100px;
    & > div {
      display: flex;
      flex-direction: row;

      & > div {
        padding: 10px;
        opacity: 0.5;
        & > div {
          width: 3px;
          height: 3px;
          border-radius: 5px;
          background-color: gray;
          transition: all 1s ease-out;
        }
        &:hover > div {
          transform: scale(2);
          background-color: fuchsia !important;
          transition: all 0s ease-in;
        }
      }
    }
  }

  .about-button {
    margin-top: 20px;
    color: fuchsia;
    display: inline-block;
    svg {
      transition: all 0.3s;
      overflow: visible;
    }
    &:hover svg {
      transform: rotateZ(90deg) translateY(-5px);
    }
    & > span {
      font-size: 12px;
      color: gray;
    }
  }
`;

export const CenteredPage = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  scroll-snap-align: center;
  position: relative;

  .quote {
    font-size: 14px;
    color: gray;
    font-variant: small-caps;
    text-align: center;
    padding: 10px;
  }

  .bg_dots {
    position: absolute;
    top: -50px;
    right: 0px;
    & > div {
      display: flex;
      flex-direction: row;

      & > div {
        padding: 10px;
        opacity: 0.3;
        & > div {
          width: 3px;
          height: 3px;
          border-radius: 5px;
          background-color: gray;
          transition: all 1s ease-out;
        }
        &:hover > div {
          transform: scale(2);
          background-color: fuchsia !important;
          transition: all 0s ease-in;
        }
      }
    }
  }
  .title {
    font-size: 1.5rem;
    color: gray;
    margin-bottom: 60px;
    &.big {
      font-size: 4rem;
      color: aliceblue;
      font-weight: 700;
      margin-bottom: 20px;

      @media (max-width: 1080px) {
        font-size: 2.5rem;
      }
    }
    &.contact {
      font-size: 2rem;
      color: white;
      padding: 50px;
      text-align: center;
      & small {
        color: gray;
      }
    }
  }
  .about {
    max-width: 600px;
    text-align: center;

    @media (max-width: 600px) {
      max-width: calc(100% - 40px);
    }
    cursor: none;
    & > div > div > span {
      transition: all 0.9s ease-out;
    }
    & > div > div > span:hover {
      transition: all 0s ease-in;
      color: fuchsia;
    }
  }
  .experience {
    text-align: center;
    margin-top: 40px;

    .help-text {
      transition: all 0.3s;
      font-size: 11px;
      color: gray;
    }
    &:hover .help-text {
      opacity: 1;
    }
    &:hover h5 {
      color: fuchsia;
    }
    img {
      width: 70px;
      height: 70px;

      @media (max-width: 1080px) {
        width: 50px;
        height: 50px;
      }
    }
    h5 {
      margin: 0;
      font-variant: small-caps;
    }
  }
  .experience-modal {
    text-align: center;
    position: absolute;
    z-index: 1;
    width: 600px;
    background-color: #ffffff20;
    border-radius: 20px;

    @media (max-width: 1080px) {
      width: calc(100vw - 40px);
      max-width: 600px;
    }
    img {
      width: 100px;
      height: 100px;
      margin-top: -50px;
    }
    h4 {
      margin: 0;
      font-variant: small-caps;
    }
    a {
      text-decoration: none;
      color: bisque;
      font-variant: small-caps;
    }
    p {
      padding: 20px;
      font-size: 14px;
      max-height: 50vh;
      overflow-y: auto;
    }
  }
  .experience-modal-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(20px);
    z-index: 1;
    background-color: #00000080;
  }

  .bg_mac {
    position: absolute;
    width: 300px;
    left: 0;
    bottom: -50px;
    opacity: 0.2;
    filter: blur(3px);
    transition: 0s;
  }
  .bg_code {
    position: absolute;
    width: 500px;
    top: -200px;
    right: -10px;
    opacity: 0.2;
    filter: blur(6px);
    &.bottom {
      bottom: -100px;
      top: unset;
    }
  }
  .github-link {
    display: block;
    text-decoration: none;
    color: fuchsia;
    margin-top: 50px;
    svg {
      transition: all.3s;
    }
    &:hover svg {
      transform: translateX(20px) scale(1.2);
    }
  }
  .bg_icon {
    width: 80px;
    height: 80px;
    position: absolute;
    opacity: 0.3;
    transition: all 0.3s;
    filter: blur(2px);
    animation-name: ${breatheAnimationSmall};
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    &.react_icon {
      top: 20%;
      left: 20%;
      animation-delay: 500ms;
    }
    &.swift_icon {
      bottom: 15%;
      left: 15%;
      animation-delay: 200ms;
    }
    &.kotlin_icon {
      bottom: 35%;
      right: 15%;
    }
    &:hover {
      opacity: 0.6;
      filter: blur(0px);
    }
  }
  .divider {
    width: 30vw;
    height: 1px;
    background-color: gray;
    opacity: 0.5;
    margin-top: 50px;
    margin-bottom: 50px;
    @media (max-width: 1080px) {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }
  .language_skill_container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 1000px;
    justify-content: center;
    align-items: center;

    @media (max-width: 1080px) {
      max-width: 100vw;
    }
    .language_skill {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin: 50px 40px;
      margin-top: 0px;

      @media (max-width: 1080px) {
        margin: 25px 20px;
      }
      & > svg {
        width: 60px;
        height: 60px;
        margin-bottom: 10px;
        transition: all 0.3s;
        @media (max-width: 1080px) {
          width: 40px;
          height: 40px;
        }
        path,
        g {
          transition: all 0.3s;
        }
        &.language-reactnativejs {
          transition: all 1s;
        }
        &.language-nextjs {
          fill: white;
        }
        &.language-androidstudio {
          transform: scale(1.5);
        }
      }
      &:hover svg {
        &.language-typescript {
          & path.st1 {
            transform: scale(1.1) rotateZ(2deg);
          }
        }

        &.language-reactnativejs {
          transform: rotate(360deg);
        }
        &.language-androidstudio {
          & .android_path {
            transform: translateY(-25px) scale(1.2) translateX(-20px);
          }
        }
        &.language-xcode {
          & .xcode-hammer {
            transform: rotateZ(25deg) translateY(-20px) translateX(100px);
          }
        }
        &.language-nestjs,
        &.language-nodejs,
        &.language-nextjs,
        &.language-tailwind,
        &.language-git,
        &.language-python {
          transform: scale(1.1) rotateZ(10deg);
        }
        &.language-nextjs {
          fill: fuchsia;
        }
        &.language-css path:last-of-type,
        &.language-html path:last-of-type {
          transform: scale(1.3) translateY(-15px) translateX(-15px)
            rotateZ(7deg);
        }
      }
    }
  }
  .contact-way {
    width: 50px;
    margin: 20px;
    transition: all 0.3s;
    &:hover {
      transform: scale(1.2);
    }
  }
`;
