import React, { Dispatch, SetStateAction } from "react";
import { useSelector } from "react-redux";
import { selectPower } from "../../global-state";
import * as C from "./styles";

type Props = {
  setPower: (pwer: string) => void;
  setPortfolio: Dispatch<SetStateAction<boolean>>;
};

export const Power = ({ setPower, setPortfolio }: Props) => {
  const power = useSelector(selectPower);

  const onPowerOn = () => {
    if (power !== "size") {
      setPower("on");
      // document.getElementById("root")?.requestFullscreen();
    }
  };

  return (
    <C.Container>
      <div className="bg">
        <div className="loadingArea">
          <img
            onClick={onPowerOn}
            className="logo"
            src="/assets/images/apple-logo.png"
            alt="logo"
          />
        </div>
        <span style={{ textAlign: "center" }}>
          {power === "size"
            ? "Screen Size is not supported!"
            : "Click Apple Logo to Power on your Mac Simulation"}
          {power === "off" && (
            <>
              <br />
              Portfolio will be visible after mac is booted
            </>
          )}
        </span>
        {power === "off" && (
          <div className="portfolio" onClick={() => setPortfolio(true)}>
            Click here to view portfolio directly!
          </div>
        )}
        <span>macOS Clone By Nitesh Kumar</span>
      </div>
    </C.Container>
  );
};
