import { motion, useScroll, useTransform } from "framer-motion";
import { CenteredPage } from "./styles";
import { RefObject, useEffect, useState } from "react";
import { delay } from "./utils";
import { ReactComponent as Swift } from "./assets/swift.svg";
import { ReactComponent as Kotlin } from "./assets/kotlin.svg";
import { ReactComponent as React } from "./assets/react.svg";

const About = ({
  containerRef,
}: {
  containerRef: RefObject<HTMLDivElement>;
}) => {
  const [ref, setRef] = useState<RefObject<HTMLDivElement>>();
  const { scrollYProgress } = useScroll({
    container: ref,
  });
  const top1 = useTransform(scrollYProgress, [0.25, 0.3], [0, -200]);
  const top2 = useTransform(scrollYProgress, [0.25, 0.4], [0, -200]);
  const top3 = useTransform(scrollYProgress, [0.25, 0.5], [0, -200]);

  useEffect(() => {
    setRef(containerRef);
  }, []);

  return (
    <CenteredPage>
      <motion.div className="title" style={{ y: top1 }}>
        About Me
      </motion.div>
      <motion.div className="about">
        <motion.div style={{ y: top1 }}>
          <motion.div
            initial={{ y: 300, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={delay(0)}
            viewport={{
              margin: "-300px 0px 0px 0px",
              amount: "some",
              root: ref,
              once: true,
            }}
          >
            {`Diving into the tapestry of my coding odyssey, spanning three
          spellbinding years, I've emerged as a fervent advocate for the
          artistry of Frontend sorcery. With React Native and NextJS as my
          enchanted quills, I passionately sculpt mobile masterpieces that
          transcend the mundane.`
              .split(" ")
              .map((char, inx) => (
                <span key={char + inx}> {char}</span>
              ))}
          </motion.div>
        </motion.div>
        <br />
        <br />
        <motion.div style={{ y: top2 }}>
          <motion.div
            initial={{ y: 300, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={delay(0)}
            viewport={{
              margin: "-300px 0px 0px 0px",
              amount: "some",
              root: ref,
              once: true,
            }}
          >
            {`In the magical realm of backend alchemy, I invoke NestJS to weave
          intricate spells, conjuring microservices that dance to the rhythm of
          reliability. Yet, it's on the Frontend stage where my heart truly
          sings, where each line of code becomes a stroke on the canvas of user
          experience.`
              .split(" ")
              .map((char, inx) => (
                <span key={char + inx}> {char}</span>
              ))}
          </motion.div>
        </motion.div>
        <br />
        <br />
        <motion.div style={{ y: top3 }}>
          <motion.div
            initial={{ y: 300, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={delay(0)}
            viewport={{
              margin: "-300px 0px 0px 0px",
              amount: "some",
              root: ref,
              once: true,
            }}
          >
            {`At the helm of the App Team within the mystical confines of Bajaj Finserv Health, I
          orchestrate a symphony of innovation with a particular ardor for
          Frontend enchantment. Beyond the cold logic of code, I am a
          dreamweaver, sculpting realms where technology harmonizes with
          creativity in a boundless dance of infinite possibilities. 💫💻 Let
          the love for Frontend brilliance illuminate the digital cosmos!`
              .split(" ")
              .map((char, inx) => (
                <span key={char + inx}> {char}</span>
              ))}
          </motion.div>
        </motion.div>
      </motion.div>
      <Kotlin className="kotlin_icon bg_icon" />
      <Swift className="swift_icon bg_icon" />
      <React className="react_icon bg_icon" />
    </CenteredPage>
  );
};

export default About;
