import { motion, useScroll, useTransform } from "framer-motion";
import { CenteredPage } from "./styles";
import { RefObject, useEffect, useState } from "react";

const Projects = ({
  containerRef,
}: {
  containerRef: RefObject<HTMLDivElement>;
}) => {
  const [ref, setRef] = useState<RefObject<HTMLDivElement>>();

  const { scrollYProgress } = useScroll({
    container: ref,
  });
  const top1 = useTransform(scrollYProgress, [0.75, 0.8], [0, -200]);
  const top2 = useTransform(scrollYProgress, [0.75, 1], [0, -200]);

  useEffect(() => {
    setRef(containerRef);
  }, []);

  return (
    <CenteredPage>
      <motion.div className="title big" style={{ y: top1 }}>
        Projects
      </motion.div>
      <motion.div className="about" style={{ cursor: "default" }}>
        <motion.div className="quote" style={{ y: top1 }}>
          In the ever-evolving tech symphony, projects are the avant-garde
          compositions, blending code, creativity, and innovation to sculpt the
          future.
        </motion.div>
        <motion.a
          style={{ y: top2 }}
          href="https://github.com/niteshdangi"
          target="_blank"
          className="github-link"
        >
          View Projects on GitHub{" "}
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 16 16"
            width="16"
            height="16"
            fill="none"
            stroke="currentColor"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path d="M9 16l6-6-6-6" />
          </svg>
        </motion.a>
      </motion.div>

      <img
        src="/assets/images/portfolio/code-blocks.png"
        className="bg_code bottom"
      />
      <motion.div className="bg_dots">
        {Array.from({ length: 10 }, (_, inx) => (
          <div key={inx}>
            {Array.from({ length: 30 }, (__, ind) => (
              <div key={ind}>
                <div />
              </div>
            ))}
          </div>
        ))}
      </motion.div>
    </CenteredPage>
  );
};

export default Projects;
